.window {
	box-shadow: inset -1px -1px #0a0a0a,inset 1px 1px #2d2d2d,inset -2px -2px rgb(39, 39, 39),inset 2px 2px #000000;
	background: #181816;
}

.window-xp-default-theme {
  background: #ece9d8;
  color: #222;
}

.title-bar {
	font-family: Trebuchet MS;
	background: linear-gradient(180deg,#1c54b1,#1c54b1 8%,#1c54b1 40%,#1c54b1 88%,#1c54b1 93%,#1c54b1 95%,#003dd7 96%,#1c54b1);
	padding: 3px 5px 3px 3px;
	border-top: 1px solid #34609f;
	border-left: 1px solid #34609f;
	border-right: 1px solid #34609f;
	border-top-left-radius: 8px;
	border-top-right-radius: 7px;
	font-size: 13px;
	text-shadow: 1px 1px #34609f;
	height: 21px;
}

//////////

.outer-page-container {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: auto;
  flex-shrink: 0;
}

:root {
  --rounding: 20px;
  --top-rounding: var(--rounding);
  --bottom-rounding: var(--rounding);
}

.page-container {
  max-width: 640px;
  margin: auto;
  background-color: #132e472f;
  padding: 25px;
  border-radius: var(--top-rounding) var(--top-rounding) var(--bottom-rounding) var(--bottom-rounding);

  flex-grow: 1;

  @media (width <= 700px){
    width: 80vw;
  }
}

//////////

nav.nav {
  margin-left: 10px;
}

nav.nav > li {
  list-style: none;
}

a {
  color: #6df0c9;
}

li.active a {
  color: lightgray;
  cursor: default;
}

//////////

span.fun-em {
  font-size: 1.5cqh;
  background: -webkit-linear-gradient(-90deg, #bbe1ff, #6df0c9 80%);
  background-clip: inherit;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

//////////

ul.links-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 1rem;
}

//////////

img.content-img {
  max-width: 100%;
}

:root {
  --image-rounding: 10px;
}

img.rounded-img {
  border-radius: var(--image-rounding);
}

em.img-caption {
  text-align: center;
  font-size: 0.8cqh;
}

div.code-wrap > div > pre > code {
  white-space: pre-wrap;
  overflow-wrap: anywhere;
}

p.post-meta {
  font-size: smaller;
}

////////////////////////

div.badges-container {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

div.badges-container > div.badges {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2px;
  line-height: 2rem;
  justify-content: space-evenly;
}

// div.badges-container > div.badges > div.badge {
//   width: 100%;
// }

div.badges-container > div.badges > div.badge > img, div.badges-container > div.badges > div.badge > a > img {
  width: 80px;
}

div.badges-container > div.badges > ul > li {
  list-style: none;
}

////////////////////////
#sparkle-container {
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.sparkle {
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: white;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
  animation: sparkle-animation 0.6s ease-out forwards;
}

@keyframes sparkle-animation {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(10);
  }
}

